<template>
  <!-- v-app은 vuetify가 제공하는 helper class style을 사용하기 위해 꼭 필요한 최상단 요소 -->
  <v-app ref="app">
    <v-app-bar-elastic
      class="area rounded-pill ma-3 elevation-8"
      app
      hide-on-scroll
    >
      <app-bar-items></app-bar-items>
    </v-app-bar-elastic>

    <v-main class="backdrop">
      <v-container
        fluid
        class="pt-14 pb-16"
        style="height: 100%"
        :style="vContainerStyle"
      >
        <transition name="fade" mode="out-in" appear>
          <router-view></router-view>
        </transition>
      </v-container>
    </v-main>

    <v-footer app absolute class="elevation-8" padless>
      <v-card tile width="100%" class="area text-center">
        <v-card-text>
          <v-btn v-for="icon in icons" :key="icon" class="mx-4" icon>
            <v-icon size="24px">
              {{ icon }}
            </v-icon>
          </v-btn>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-text>
          <strong>{{ new Date().getFullYear() }} — 예쁜 제미넴</strong>
        </v-card-text>
      </v-card>
    </v-footer>
  </v-app>
</template>

<script>
/* global cookies cssVarsPonyfill*/

export default {
  data() {
    return {
      icons: ["mdi-home", "mdi-email", "mdi-calendar", "mdi-delete"],
      contentTitle: "Untitled",
      settings: {
        brightMode: false,
      },
    };
  },
  computed: {
    vContainerStyle() {
      return {
        maxWidth: "1280px",
      };
    },
  },
  methods: {},
  watch: {
    contentTitle(newValue) {
      // 콘텐츠 제목 변수 contentTitle 변경에 따라 웹 페이지 제목도 변경
      if (newValue == "") {
        document.title = "Zeminem";
      } else {
        document.title = newValue + " - Zeminem";
      }
    },
    settings: {
      // This will let Vue know to look inside the array
      deep: true,
      handler(newValue, oldValue) {
        this.$vuetify.theme.dark = !newValue.brightMode;
        cookies.set("settings", newValue, { expires: 365 });

        // 이건 IE11에서 CSS var가 가능하게 해 주는 Ponyfill(호환성 확보)
        //CSS var가 봐뀌어도 IE11은 탐지를 못 함. 그래서 직접 값을 변경해주는 라이브러리임.
        // https://jhildenbiddle.github.io/css-vars-ponyfill/#/
        cssVarsPonyfill({
          // Targets
          rootElement: document,
          shadowDOM: true,

          // Sources
          include: "link[rel=stylesheet],style",
          exclude: "",
          variables: {},

          // Options
          onlyLegacy: true,
          preserveStatic: true,
          preserveVars: false,
          silent: false,
          updateDOM: true,
          updateURLs: false,
          watch: false,
        });
      },
    },
  },
  created() {
    //저장된 쿠키를 읽어서 반영
    Object.assign(this.settings, cookies.getJSON("settings"));
  },
  mounted() {},
  destroyed() {},
};
</script>

<style lang="scss" scoped>
.spacer {
  &-top {
    height: 84px;
  }
  &-bottom {
    height: 180px;
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>